@use "../../../abstracts/variables" as *;

// Base Navigation Styles
nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1100;
  width: 100%;
  background: $color-eigengrau;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 0 0 -1rem -1rem;
  transition: all 0.5s ease;
  -webkit-backdrop-filter: blur(50px);
  backdrop-filter: blur(50px);
  color: $color-text;
}

nav .wrapper {
  position: relative;
  max-width: 100%;
  padding: 0px 30px;
  height: 80px;
  line-height: 5rem;
  margin: auto;
  display: flex;
  justify-content: space-between;
}

// Logo Styles
.wrapper .logo {
  flex: 1 1 auto;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper .logo a {
  font-family: "Orbitron", "Arial", sans-serif;
  font-size: 2.1rem;
  font-weight: 900;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  transform: skewX(-8deg);
  transition: color 0.2s;
  color: $color-text;

  // Mobile responsive styles für Logo
  @media screen and (max-width: 970px) {
    font-size: 1.6rem;
  }

  @media screen and (max-width: 768px) {
    font-size: 1.4rem;
  }

  @media screen and (max-width: 480px) {
    font-size: 1.2rem;
    letter-spacing: 0.1em;
  }
}

// Navigation Links
.wrapper .nav-links {
  display: inline-flex;
}

.nav-links li {
  list-style: none;
}

.nav-links li a {
  color: $color-text;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  padding: 9px 15px;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.nav-links li a:hover {
  background: $color-eigengrau-transparent;
  color: $color-text;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.4),
    inset -2px -2px 6px rgba(255, 255, 255, 0.05);
}

.nav-links .mobile-item {
  display: none;
}

// Dropdown Menu
.nav-links .drop-menu {
  position: absolute;
  background: $color-eigengrau-transparent;
  box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.5);
  border-radius: 0 0 1rem 1rem;
  width: 300px;
  line-height: 45px;
  top: 85px;
  opacity: 0;
  visibility: hidden;
}

.nav-links li:hover .drop-menu,
.nav-links li:hover .mega-box {
  transition: all 0.3s ease;
  top: 70px;
  opacity: 1;
  visibility: visible;
}

.drop-menu li a {
  width: 100%;
  display: block;
  padding: 0 0 0 15px;
  font-weight: 400;
  border-radius: 0px;
}

// Mobile Menu Button
.wrapper .btn {
  color: $color-text;
  font-size: 20px;
  cursor: pointer;
  display: none;
}

.wrapper .btn.close-btn {
  position: absolute;
  right: 30px;
  top: 10px;
}

// Mega Menu Base
.mega-box {
  position: absolute;
  left: 0;
  width: 100%;
  top: 85px;
  opacity: 0;
  visibility: hidden;
  z-index: 888;
  background: $color-eigengrau;
  border-radius: 0 0 1rem 1rem;
}

.mega-box .content {
  padding: 25px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 40rem;
  box-shadow: 24px 48px 48px -16px rgba(0, 0, 0, 0.38),
    -24px 48px 48px -16px rgba(0, 0, 0, 0.38),
    0 32px 64px -16px rgba(0, 0, 0, 0.22);
  z-index: 500;
  border-radius: 0 0 1rem 1rem;
}

// Utility Classes
.marke-icon {
  margin-top: 1.5rem;
  height: 40px;
  margin-right: 10px;
}

nav input {
  display: none;
}

.blur-overlay {
  position: fixed;
  inset: 0;
  z-index: 800;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s;
  -webkit-backdrop-filter: blur(18px);
  backdrop-filter: blur(18px);
}

.blur-overlay.active {
  opacity: 1;
  pointer-events: auto;
}

// Mobile Menu Button Styles
.wrapper .btn {
  color: $color-text;
  font-size: 20px;
  cursor: pointer;
  display: none;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  padding: 10px;
  background: $color-primary;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  text-align: center;
  transition: all 0.3s ease;

  z-index: 130000000;
}

.wrapper .btn:hover {
  background: $color-secondary-light;
}

// CSS Hamburger Icon (replaces FontAwesome)
.wrapper .btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 2px;
  background: $color-text;
  box-shadow: 0 -6px 0 $color-text, 0 6px 0 $color-text;
  transition: all 0.3s ease;
}

// Hide FontAwesome icon if present
.wrapper .btn i {
  display: none;
}

.wrapper .btn.close-btn {
  position: absolute;
  right: 30px;
  top: 10px;
}

// Mobile Responsive Styles
@media screen and (max-width: 970px) {
  .wrapper .btn {
    display: block;
  }

  .wrapper .nav-links {
    position: fixed;
    height: 100vh;
    width: 100vw;
    max-width: 100vw;
    top: 0;
    right: -100vw;
    background: $color-eigengrau;
    display: block;
    padding: 40px 20px 80px 20px; // Bottom padding hinzugefügt für Kontakt-Button
    line-height: 45px; // Reduziert von 50px
    overflow-y: auto;
    box-shadow: none;
    transition: all 0.3s ease;
    z-index: 1200;
  }

  // Custom scrollbar for mobile navigation
  ::-webkit-scrollbar {
    width: 10px;
  }

  ::-webkit-scrollbar-track {
    background: $color-eigengrau;
  }

  ::-webkit-scrollbar-thumb {
    background: $color-secondary-dark;
  }

  #menu-toggle:checked ~ .nav-links {
    right: 0;
    left: 0;
  }

  // Keep button visible and change appearance when menu is open
  #menu-toggle:checked ~ .btn.menu-btn {
    display: block;
    background: $color-secondary; // Different color when menu is open
    z-index: 9999999; // Ensure it stays on top
  }

  // Change hamburger icon to X when menu is open
  #menu-toggle:checked ~ .btn.menu-btn::before {
    transform: translate(-50%, -50%) rotate(45deg);
    box-shadow: 0 0 0 $color-text;
    width: 20px;
  }

  #menu-toggle:checked ~ .btn.menu-btn::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
    width: 20px;
    height: 2px;
    background: $color-text;
  }

  .nav-links li {
    margin: 10px auto; // Reduziert von 20px auf 10px
    text-align: center;
    max-width: 600px;
  }

  .nav-links li a {
    padding: 10px 25px; // Reduziert von 15px 30px
    display: block;
    font-size: 20px; // Reduziert von 24px
    text-align: center;
    border-radius: 8px; // Reduziert von 10px
    margin: 6px 0; // Reduziert von 10px
  }

  .nav-links .drop-menu {
    position: static;
    opacity: 1;
    top: 65px;
    visibility: visible;
    padding-left: 20px;
    width: 100%;
    max-height: 0px;
    overflow: hidden;
    box-shadow: none;
    transition: all 0.3s ease;
  }

  #showDrop:checked ~ .drop-menu,
  #showDrop1:checked ~ .drop-menu,
  #showDrop2:checked ~ .drop-menu,
  #showDrop3:checked ~ .drop-menu,
  #showDrop4:checked ~ .drop-menu,
  #showMega:checked ~ .mega-box,
  #showMega2:checked ~ .mega-box {
    max-height: 100%;
  }

  .nav-links .desktop-item {
    display: none;
  }

  .nav-links .mobile-item {
    display: block;
    color: $color-text;
    font-size: 20px; // Reduziert von 24px
    font-weight: 500;
    padding: 10px 25px; // Reduziert von 15px 30px
    margin: 6px auto; // Reduziert von 10px
    max-width: 600px;
    text-align: center;
    cursor: pointer;
    border-radius: 8px; // Reduziert von 10px
    transition: all 0.3s ease;

    // Button-style like contact button
    background: rgba(35, 38, 40, 0.8);
    border: 1.5px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.18);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
  }

  .nav-links .mobile-item:hover {
    background: rgba(35, 39, 43, 0.9);
    border-color: $color-primary;
    box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.22);
    transform: translateY(-1px);
  }

  .drop-menu li {
    margin: 0;
  }

  .drop-menu li a {
    border-radius: 5px;
    font-size: 18px;
    color: $color-secondary;
  }

  .mega-box {
    position: static;
    top: 0;
    opacity: 1;
    visibility: visible;
    max-height: 0px;
    overflow: hidden;
    transition: all 0.3s ease;
    width: 100%;
  }

  .mega-box .content {
    box-shadow: none;
    flex-direction: column;
    height: auto;
    padding: 0px;
    max-width: 100%;
    width: 100%;
  }
}

